import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import { StyledGrid, StyledArt } from './StyledAbout';
import { Section, Container } from '@components/global';
const About = () => (
  <StaticQuery
    query={graphql`
      query {
        art_certification: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "certification" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }

        art_screen: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "screen" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }

        art_chat_box: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "chat_box" }
        ) {
          childImageSharp {
            fluid(maxWidth: 760) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    `}
    render={data => (
      <Section id="about">
        <Container>
          <StyledGrid>
            <div>
              <h2></h2>
              <p>
                
              </p>
            </div>
            <StyledArt>
              <Img fluid={data.art_certification.childImageSharp.fluid} />
            </StyledArt>
          </StyledGrid>
          <StyledGrid inverse>
            <StyledArt>
              <Img fluid={data.art_screen.childImageSharp.fluid} />
            </StyledArt>
            <div>
              <h2></h2>
              <p>
                
              </p>
            </div>
          </StyledGrid>
          <StyledGrid>
            <div>
              <h2></h2>
              <p>
                
              </p>
            </div>
            <StyledArt>
              <Img fluid={data.art_chat_box.childImageSharp.fluid} />
            </StyledArt>
          </StyledGrid>
        </Container>
      </Section>
    )}
  />
);

export default About;
